import logo from './logo.svg';
import React, { useEffect, useState } from 'react';
import './index.css';
import Header from './tabs/Header';
import Main from './tabs/Main';
import ContactOverlay from './tabs/ContactOverlay';
import axios from 'axios';
import { initializeApp } from 'firebase/app';
import { getAnalytics, logEvent } from 'firebase/analytics';

const backend = 'https://34.138.41.238:3000';
function App() {
	const [contactShow, showContactOverlay] = useState(false);
	const app = initializeApp({
    apiKey: 'AIzaSyD654D1qglm6DkjF5uaNSEINEmbkK8vvnM',
    authDomain: 'resume-site-d4d5c.firebaseapp.com',
    databaseURL: 'https://resume-site-d4d5c-default-rtdb.firebaseio.com',
    projectId: 'resume-site-d4d5c',
    storageBucket: 'resume-site-d4d5c.appspot.com',
    messagingSenderId: '258172668084',
    appId: '1:258172668084:web:d3dd8114bd9d272e966fe1',
    measurementId: 'G-TFJTZXMG4Q',
  });
  const analytics = getAnalytics(app);

  useEffect(()=>{
    logEvent(analytics,'page_view')
  },[])
	function handleContactOverlay() {
		showContactOverlay(!contactShow);
	}

	function handleContactSubmit(event) {
		event.preventDefault();
		handleContactOverlay();
		let target = event.target;
		console.log('target', event.target);
		const newEmail = {
			name: target.fname.value,
			email: target.femail.value,
			subject: target.fsubject.value,
			content: target.fmessage.value,
		};
		axios.post(`${backend}/resume-site/sendEmail`, {
			name: target.fname.value,
			email: target.femail.value,
			subject: target.fsubject.value,
			content: target.fmessage.value,
		});
		console.log(newEmail);
	}

	return (
		<div className="container">
			{!contactShow && <Header />}
			{!contactShow && <Main onContact={handleContactOverlay} />}
			{contactShow && <ContactOverlay onFormSubmit={handleContactSubmit} />}
		</div>
	);
}

export default App;
