import styles from '../css-tab-modules/OtherProjects.module.css'
import OtherProject from '../templates/OtherProject';
import React from 'react';




//https://console.firebase.google.com/u/0/project/resume-site-d4d5c/database/resume-site-d4d5c-default-rtdb/data/~2F
const OtherProjects = (props) => {
    const projects = props.data;

    const projMap = projects.map(project => {
        return <OtherProject id={project.key} name={project.name} description={project.description} github={project.github} tools={project.tools} />
    });

    return (
        <div id={styles.otherProjects}>
            <h2 id={styles.other_project_header}>Some Other Projects</h2>

            <ul className={styles.projectContainer}>
                {projMap}
            </ul>

        </div>
    );
}

export default OtherProjects;