import styles from '../css-template-modules/EducationText.module.css'
import React from 'react';



function EducationText(props) {


    const list = props.list;
    const listItems = list.map((item) => {
        if (item === "Courses in:") {
            const courses = props.courseList;
            const courseList = courses.map((course) => {
                return (<li>{course}</li>);

            });
            return (<li>{item}
                <ul style={{listStyle:"none"}}>
                    {courseList}
                </ul>

            </li>);
        }
        else
            return <li key={item}>{item}</li>;
    }
    );

    return (

        <div className={styles.education_text} selected>
            <h3>{props.title}</h3>
            <p className={styles.duration + " code"}>{props.duration}</p>
            <ul>
                {listItems}

            </ul>
        </div>
    );
}

export default EducationText;