import FeaturedProject from '../templates/FeaturedProject';
import OtherProjects from './OtherProjects';
import React from 'react';
import { useState, useEffect } from 'react';


function Projects(props) {
    const [isLoading, setIsLoading] = useState(true);
    const [featuredProjects, setFeatureProjects] = useState([]);
    const [loadedProjects, setLoadedProjects] = useState([]);

    useEffect(() => {
        fetch('https://resume-site-d4d5c-default-rtdb.firebaseio.com/projects.json'
        ).then((response) => {
            return response.json();
        }).then((data) => {
            const projects = [];
            const featuredProjectsArray = [];
            for (const key in data) {
                const project = {
                    id: key,
                    ...data[key]
                }
                if (project.feature) {
                    featuredProjectsArray.push(project);
                } else
                    projects.push(project);
            }
            setLoadedProjects(projects);
            featuredProjectsArray.sort((a,b) => (a.feature_id<b.feature_id) ? -1: 1);
            setFeatureProjects(featuredProjectsArray);
            setIsLoading(false);

        });
    }, []);


    const featuredProjMap = featuredProjects.map(project => {
        if (project.feature_id % 2 == 0) {
            return (<FeaturedProject 
            id={project.key}
            name={project.name} 
            description={project.description} 
            github={project.github} 
            tools={project.tools} 
            float="right"
            margins="0px 0px 0px 25px"/>);
        } else {
            return (<FeaturedProject 
                id={project.key}
                name={project.name} 
                description={project.description} 
                github={project.github} 
                tools={project.tools} 
                float="left"
                margins="0px 25px 0px 0px"/>);
        }

    });


    if (isLoading) {
        return (<div id="projects" className="contentBlock center code"><p>Loading...</p></div>);
    }

    return (
        <div id="projects" className="contentBlock">
            <h2 className="header after">Things I've built</h2>
            {featuredProjMap}
            {/* <FeaturedProject
                name="Restaurant Rater"
                description="An interactive app allowing a user to search for restaurants based on cuisine and ratings"
                github="https://github.com/plaartz/cs400-restaurant-app"
                tools={["Java", "JSON", "Backend"]}
                float="right"
                margins="0px 0px 0px 25px"
                 />
            <FeaturedProject
                name="Device Check In"
                description="Using a raspberry pi, camera, and a display, displays GUI prompts to check an item in/out using QR Codes 
                and a google sheet containing item ID and check in information."
                tools={["Python", "Raspberry Pi", "Google API", "Linux"]}
                float="left"
                margins="0px 25px 0px 0px"
                github="https://github.com/plaartz/checkIn" />
            <FeaturedProject
                name="Galaga VR Clone"
                description="Using C# and unity I created a clone of Galaga that can be played in both VR as well as on a desktop"
                tools={["C#", "Unity", "VR", "Blender"]}
                float="right"
                margins="0px 0px 0px 25px"
                github="https://github.com/plaartz/unity3d-galaga-scripts" /> */}

            <OtherProjects
                data={loadedProjects} />


        </div>

    );
}


export default Projects;